<template>
  <view>
    <view class="box">
      <view>
        <image class="box-icon" mode="widthFix" :src="$kt.file.byPath('/image/icon/star.svg')"></image>
      </view>
      <view style="font-weight: bold">
        {{ text }}
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      text: "",
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.text = "获取登录状态";
      // 获取token存储的时间
      let tokenTime = this.$kt.request.getTokenStorageTime();
      // 如果token存储的时间为空,则跳转到登录页面
      if (tokenTime == null) {
        uni.redirectTo({
          url: "/pages/login/login",
        });
        return false;
      }
      // 如果token存储时间小于5天，则直接进入首页
      if (new Date().getTime() - tokenTime < 5 * 24 * 60 * 60 * 1000) {
        this.text = "登录状态获取完成";
        // 判断用户信息的头像或昵称是否为空
        if (this.$ovo.getSelf()==null || this.$ovo.getSelf().user==null || this.$ovo.getSelf().user.fileIdOfAvatar == null || this.$ovo.getSelf().user.nickname == null) {
          this.getLoginStatus();
          return false;
        }


        // 判断之前还有没有页面,uniapp
        if (getCurrentPages().length > 1) {
          uni.$emit("loginStatus",true);
          uni.navigateBack();
          return false;
        }
        uni.$emit("loginStatus",true);
        uni.redirectTo({
          url: "/pages/body/static",
        });
        return false;
      }

      this.getLoginStatus();
      this.text = "登录状态获取完成";
    },
    // 获取登录状态
    async getLoginStatus() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.system.user.getSelf,
          isNoInterceptor: true,
          stateSuccess: (res) => {

            // uni.$emit 确认登录状态
            uni.$emit("loginStatus", true);


            // 设置登录信息
            this.$selfInfo.set(res.data);
            // 获取绑定信息
            this.$kt.request.request({
              api: this.$api.business.ovo.userBind.getSelf,
              stateSuccess: (res1) => {
                // 设置绑定信息
                this.$ovo.setSelf(res1.data);
                // 判断用户信息的头像或昵称是否为空
                if (res.data.fileIdOfAvatar == null || res.data.nickname == null) {
                  uni.redirectTo({
                    url: "/pages/init/selfInfoInit",
                  });
                  return false;
                }


                // 判断之前还有没有页面,uniapp
                if (getCurrentPages().length > 1) {
                  uni.navigateBack();
                  return false;
                }
                uni.reLaunch({
                  url: "/pages/body/static",
                });
                resolve("");
              },
            });
          },
          stateFail: (res) => {
            uni.redirectTo({
              url: "/pages/login/login",
            });
            reject("");
          },
        });
      });
    },
    async getAppFront() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.system.dictI18n.getAppFront,
          stateSuccess: (res) => {
            uni.$emit("getDictOfAppFront", res.data);
            this.$kt.i18n.setAppFront(res.data);
            resolve("");
          },
        });
      });
    },
    // 初始化性取向字典
    async initSexualOrientationMap() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.business.ovo.sexualOrientation.getMap,
          stateSuccess: (res) => {
            this.$ovo.setSexualOrientationMap(res.data);
            resolve("");
          },
        });
      });
    },
    // 初始化SM属性字典
    async initSadomasochismAttrMap() {
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.business.ovo.sadomasochismAttr.getMap,
          stateSuccess: (res) => {
            this.$ovo.setSadomasochismAttrMap(res.data);
            resolve("");
          },
        });
      });
    },
    // 获取当前时间戳
    async initGMTTime() {
      // system/util/getGMTTime
      await new Promise((resolve, reject) => {
        this.$kt.request.request({
          api: this.$api.system.util.getGMTTime,
          stateSuccess: (res) => {
            this.$system.setGMTTime(res.data);
            resolve("");
          },
        });
      });
    },
  },
};
</script>

<style lang="scss">
.box {
  // 黑色滤镜
  position: fixed;
  filter: grayscale(100%);
  text-align: center;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  font-size: 40rpx;
}

.box-icon {
  width: 200rpx;
  height: 200rpx;
  // 旋转动画
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
</style>
